
const Web3 = require('web3');
const Tx = require('ethereumjs-tx').Transaction;
import Common from 'ethereumjs-common'

const customCommon = Common.forCustomChain(
  'mainnet',
  {
    name: 'bsc-test',
    networkId: 97,
    chainId: '0x61',
  },
  'petersburg',
)
// const web3 = new Web3(new Web3.providers.HttpProvider("http://120.25.205.64"));
const web3 = new Web3(new Web3.providers.HttpProvider("https://data-seed-prebsc-1-s2.binance.org:8545/"));
console.log(web3)
import ercabi from "./abi.json"
let key = '445c9d24aae8ba3d5a9bbd83d62d2dcf40552438614441a82fc070631b2c602b'
let jsonAddress = '0x2A5D18D7Fafa938dAA8AADf39B472054d3dE87D1'
let urlAddress= '0x838e69c4dF6EE9a76c8AF67776f163dBbb5471Cd'
const abi = ercabi.abi
let ercMethods = {
  safeMint(tokenId,json,callback,type){
    let registryAddress = type === 1?jsonAddress:urlAddress
    var privateKey = Buffer.from(key,'hex');
    const account = web3.eth.accounts.privateKeyToAccount("0x"+key);
    const address = account.address
    var myContract = new web3.eth.Contract(abi,registryAddress)
    web3.eth.getTransactionCount(address).then(
        nonce => {
            console.log("nonce: ",nonce)
            const txParams = {
              nonce: nonce,
              gasPrice: web3.utils.toHex(web3.utils.toWei('10','Gwei')),
              gasLimit: '0x271000',
              to: registryAddress,
              data: myContract.methods.safeMint('0xbE176E3e9441934eD18E1534F3CC4C9AAE343979',tokenId,json).encodeABI(), 
            
            }
            const tx = new Tx(txParams,{ common: customCommon },)
            let txDetaile = tx.sign(privateKey)
            const serializedTx = tx.serialize()
            web3.eth.sendSignedTransaction('0x' + serializedTx.toString('hex'),(error, result) => {
              // console.log(error, result)
              if(result){
                callback({
                  code:200,
                  data:result
                })
              }else{
                callback({
                  code:400,
                  data:error
                })
              }
              
            }).on('receipt',e => console.log(e));
              
        },
        e => console.log(e)
    )
  },
  tokenURI(tokenId,callBack,type){
    let registryAddress = type === 1?jsonAddress:urlAddress
    console.log(type)
    var myContract = new web3.eth.Contract(abi,registryAddress)
    myContract.methods.tokenURI(tokenId).call((err, result)=>{
      if(err) {
        callBack({
          code:400,
          data:'0',
        })
      }else {
        console.log(result)
        let str = result
        if(type === 1){
          var base64 = result.substring(29);
          str = window.atob(base64);
        }
        callBack({
          code:200,
          data:  str
        })
      }
    })
  },
  updateItem(tokenId,json,callBack,type){
    let registryAddress = type === 1?jsonAddress:urlAddress
    var privateKey = Buffer.from(key,'hex');
    const account = web3.eth.accounts.privateKeyToAccount("0x"+key);
    const address = account.address
    var myContract = new web3.eth.Contract(abi,registryAddress)
    web3.eth.getTransactionCount(address).then(
        nonce => {
            console.log("nonce: ",nonce)
            const txParams = {
              nonce: nonce,
              gasPrice: web3.utils.toHex(web3.utils.toWei('10','Gwei')),
              gasLimit: '0x271000',
              to: registryAddress,
              data: myContract.methods.updateItem(tokenId,json).encodeABI(), 
            }
            const tx = new Tx(txParams,{ common: customCommon },)
            let txDetaile = tx.sign(privateKey)
            const serializedTx = tx.serialize()
            web3.eth.sendSignedTransaction('0x' + serializedTx.toString('hex'),(error, result) => {
              // console.log(error, result)
              if(result){
                callBack({
                  code:200,
                  data:result
                })
              }else{
                callBack({
                  code:400,
                  data:error
                })
              }
              
            }).on('receipt',e => console.log(e));
              
        },
        e => console.log(e)
    )
  },
}



export default ercMethods