<template>
  <div class="home">
    <ul class="nav">
        <li class="btn " :class="navIndex === 1 && 'active'" @click="setNavIndex(1)" >合约存储Json</li>
        <li class="btn " :class="navIndex === 2 && 'active'" @click="setNavIndex(2)">合约存储指向Json的Url</li>
    </ul>
    <div v-if="navIndex === 1" class="box">
      <div >
        <h3>铸造</h3>
        <textarea v-model="jsonDetails.data"  rows="10" placeholder="请输入Json字符串,如：{'name':'GameItemDemo #1','description':'game item demo description','image':'https://github.githubassets.com/favicons/favicon.png','attributes':{'category':'sword','damage':'1000','crit_rate':'0.2'}}"></textarea>
        <input  v-model="jsonDetails.tokenId" type="text" placeholder="请输入Token Id">
        <div>
          <button @click="safeMint(1)">铸造 <span v-if="safeMintLading" class="loading"></span> </button>
          <p v-if="jsonDetails.hash">交易Hash: <a  @click="openHash(jsonDetails.hash)">{{jsonDetails.hash}}</a> </p>
        </div>
      </div>
      <div>
        <h3>查询</h3>
        <input type="text" v-model="tokenId" placeholder="请输入Token Id">
        <p v-if="msg">
          <span>查询结果：</span>
            {{msg}}
        </p>
        <button @click="tokenURI(1)">查询 <span v-if="tokenURILoading" class="loading"></span> </button>
      </div>
      <div >
        <h3>修改</h3>
        <textarea v-model="editDetails.data"  rows="10" placeholder="请输入Json字符串"></textarea>
        <input  v-model="editDetails.tokenId" type="text" placeholder="请输入Token Id">
        <div>
          <button @click="updateItem(1)">修改 <span v-if="updateItemLading" class="loading"></span> </button>
          <p v-if="editDetails.hash">交易Hash: <a  @click="openHash(editDetails.hash)">{{editDetails.hash}}</a> </p>
        </div>
      </div>
    </div>
    <div v-if="navIndex === 2" class="box">
      <div >
        <h3>铸造</h3>
        <input  v-model="jsonDetails.data" type="text" placeholder="请输入Url，（该url收到get请求时应返回道具的json属性字符串，链接示例：https://{domain}/gameItems/{tokenId})">
        <input  v-model="jsonDetails.tokenId" type="text" placeholder="请输入Token Id">
        <div>
          <button @click="safeMint(2)">铸造 <span v-if="safeMintLading" class="loading"></span> </button>
          <p v-if="jsonDetails.hash">交易Hash: <a  @click="openHash(jsonDetails.hash)">{{jsonDetails.hash}}</a> </p>
        </div>
      </div>
      <div>
        <h3>查询</h3>
        <input type="text" v-model="tokenId" placeholder="请输入Token Id">
        <p v-if="msg">
          <span>查询结果：</span>
            {{msg}}
        </p>
        <button @click="tokenURI(2)">查询 <span v-if="tokenURILoading" class="loading"></span> </button>
      </div>
      <div >
        <h3>修改</h3>
        <input  v-model="editDetails.data" type="text" placeholder="请输入Url">
        <input  v-model="editDetails.tokenId" type="text" placeholder="请输入Token Id">
        <div>
          <button @click="updateItem(2)">修改 <span v-if="updateItemLading" class="loading"></span> </button>
          <p v-if="editDetails.hash">交易Hash: <a  @click="openHash(editDetails.hash)">{{editDetails.hash}}</a> </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import HelloWorld from '@/components/HelloWorld.vue'

export default {
  name: 'Home',
  components: {
    HelloWorld
  },
  data() {
    return {
      navIndex:1,
      jsonDetails:{
        data:'',
        tokenId:'',
        hash:''
      },
      editDetails:{
        data:'',
        tokenId:'',
        hash:''
      },
      msg:'',
      tokenId:'',
      updateItemLading:false,
      safeMintLading:false,
      tokenURILoading:false,
    }
  },
  mounted(){
    // this.getListDetaile()
  },
  methods:{
    setNavIndex(index){
      if(this.navIndex===index||this.updateItemLading||this.safeMintLading||this.tokenURILoading)return
      this.navIndex = index
      this.jsonDetails = {
        data:'',
        tokenId:'',
        hash:''
      }
      this.editDetails = {
        data:'',
        tokenId:'',
        hash:''
      }
      this.msg = ""
      this.tokenId = ''
    },
    safeMint(type){
      if(this.safeMintLading)return
      if(!this.jsonDetails.data.trim()){
        alert(`请输入${ type===1?'Json字符串':'Url' }`)
        return
      }else if(!this.jsonDetails.tokenId.trim()){
        alert('请输入Token Id')
        return
      }
      this.safeMintLading = true
      this.ercMethods.safeMint(this.jsonDetails.tokenId,JSON.stringify(this.jsonDetails.data),res=>{
        console.log(res)
        if(res.code==200){
          this.jsonDetails.hash = res.data
          
        }else{
          this.jsonDetails.hash = ''
          alert('交易失败')
        }
        this.safeMintLading = false
      },type)
    },
    updateItem(type){
      if(this.updateItemLading)return
      if(!this.editDetails.data.trim()){
        alert(`请输入${ type===1?'Json字符串':'Url' }`)
        return
      }else if(!this.editDetails.tokenId.trim()){
        alert('请输入Token Id')
        return
      }
      this.updateItemLading = true
      this.ercMethods.updateItem(this.editDetails.tokenId,JSON.stringify(this.editDetails.data),res=>{
        console.log(res)
        if(res.code==200){
          this.editDetails.hash = res.data
          
        }else{
          this.editDetails.hash = ''
          alert('交易失败')
        }
        this.updateItemLading = false
      },type)
    },
    tokenURI(type){
      if(!this.tokenId.trim()){
        alert('请输入Token Id')
        return
      }
      if(this.tokenURILoading)return
      this.tokenURILoading = true
      this.ercMethods.tokenURI(this.tokenId.trim(),res=>{
        // console.log(res)
        if(res.code == 200){
          // console.log(JSON.parse(res.data))
          this.msg = type===1?JSON.parse(res.data):res.data
        }else{
          alert('查询失败，检查Token Id是否正确')
        }
        this.tokenURILoading = false
      },type)
    },
    openHash(hash){
      window.open(`https://testnet.bscscan.com/tx/${hash}`)
    },
    
   
  }
}
</script>
